"use client";

import { useEffect, useState } from "react";
import { getApiUrl, getAssetUrl } from "@/lib/utils";

export default function GuestMyConversions() {
    const [conversions, setConversions] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);

    const fetchConversions = () => {
        fetch(getApiUrl("/api/guest-conversions"))
            .then(res => res.json())
            .then(data => {
                if (data.success) {
                    setConversions(data.conversions);
                }
                setLoading(false);
            })
            .catch(() => setLoading(false));
    };

    useEffect(() => {
        fetchConversions();
    }, []);

    return (
        <div className="space-y-6 animate-fade-in">
            {/* Header */}
            <div>
                <h1 className="text-4xl font-bold text-orange-950 tracking-tighter uppercase">Alih Audio Saya</h1>
                <p className="text-orange-900/50 text-xs font-medium uppercase tracking-widest mt-1">Daftar audio yang pernah di-generate tanpa login</p>
            </div>

            <div className="glass rounded-[32px] p-6 shadow-lg border border-orange-200/50 mt-6 relative overflow-hidden">
                {/* Visual Glow Ornament */}
                <div className="absolute top-0 right-0 w-64 h-64 bg-orange-500/10 rounded-full blur-[80px]"></div>

                {loading ? (
                    <div className="flex justify-center py-20 relative z-10">
                        <div className="w-8 h-8 rounded-full border-2 border-orange-500 border-t-transparent animate-spin shadow-[0_0_15px_rgba(249,115,22,0.5)]"></div>
                    </div>
                ) : conversions.length === 0 ? (
                    <div className="text-center py-20 relative z-10">
                        <p className="text-sm font-semibold text-zinc-400 uppercase tracking-widest">Belum ada riwayat konversi</p>
                    </div>
                ) : (
                    <div className="space-y-4 max-h-[70vh] overflow-y-auto pr-2 custom-scrollbar relative z-10">
                        {conversions.map(conv => (
                            <div key={conv.id} className="p-5 rounded-[24px] bg-white border border-orange-100 hover:border-orange-300 transition-colors shadow-sm relative group overflow-hidden">
                                <div className="absolute inset-0 bg-gradient-to-r from-orange-500/0 via-orange-500/0 to-orange-500/5 opacity-0 group-hover:opacity-100 transition-opacity"></div>
                                <div className="flex flex-col md:flex-row gap-5 justify-between items-start md:items-center relative">
                                    <div className="space-y-2 flex-1">
                                        <h3 className="font-bold text-lg text-zinc-800 line-clamp-1 group-hover:text-orange-600 transition-colors">{conv.judul || "Tanpa Judul"}</h3>
                                        <p className="text-sm text-zinc-500 line-clamp-2 leading-relaxed">{conv.text_input}</p>
                                        <div className="flex flex-wrap items-center gap-3">
                                            <span className="px-3 py-1 bg-orange-50 text-orange-600 rounded-lg text-[10px] uppercase tracking-widest font-bold">
                                                {conv.provider}
                                            </span>
                                            <span className="text-[10px] text-zinc-400 font-semibold uppercase tracking-widest">
                                                {new Date(conv.created_at).toLocaleDateString('id-ID', { year: 'numeric', month: 'long', day: 'numeric', hour: '2-digit', minute: '2-digit' })}
                                            </span>
                                        </div>
                                    </div>
                                    {conv.audio_path && (
                                        <div className="flex-shrink-0 w-full md:w-72 bg-zinc-50 p-2 rounded-2xl border border-zinc-200/50 group-hover:shadow-md transition-shadow">
                                            <audio controls src={getAssetUrl(conv.audio_path)} className="w-full h-9 opacity-90" />
                                        </div>
                                    )}
                                </div>
                            </div>
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
}
